}
body::-webkit-scrollbar-thumb {
  background-color:#000;    /* color of the scroll thumb */
  border-radius: 2px;       /* roundness of the scroll thumb */
  border: 1px solid #80808026 ;  /* creates padding around scroll thumb */
}


